.gpt3__blog-container_article {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background-color: var(--color-footer);
}

.gpt3__blog-container_article-image {
	width: 100%;
	height: 100%;
	background-color: var(--color-footer);
}

.gpt3__blog-container_article-image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.gpt3__blog-container_article-content {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 1rem 1.5rem;
	height: 100%;
}

.gpt3__blog-container_article-content p {
	font-weight: 700;
	font-size: 12px;
	line-height: 3;
	color: #fff;
}

.gpt3__blog-container_article-content h3 {
	font-weight: 800;
	font-size: 25px;
	line-height: 1.2;
	color: #fff;
	margin-bottom: 5rem;
	cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child {
	cursor: pointer;
}

@media screen and (max-width: 550px) {
	.gpt3__blog-container_article-content h3 {
		font-size: 18px;
	}
}