.gpt3__possibility {
	display: flex;
	flex-direction: row;
}

.gpt3__possibility-image {
	flex: 1;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-right: 2rem;
}

.gpt3__possibility-image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.gpt3__possibility-content {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-start;
}

.gpt3__possibility-content h4 {
	font-weight: 500;
	font-size: 16px;
	line-height: 1.88;
	color: #71E5FF;
}

.gpt3__possibility-content h4:last-child {
	color: #FF8A71;
}

.gpt3__possibility-content h2 {
	font-weight: 800;
	font-size: 34px;
	line-height: 1.32;
	margin: 1rem 0;
}

.gpt3__possibility-content p {
	font-size: 16px;
	line-height: 1.88;
	color: var(--color-text);
	margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
	.gpt3__possibility {
		flex-direction: column;
	}

	.gpt3__possibility-image {
		margin: 1rem 0;
	}

	.gpt3__possibility-content {
		margin-top: 2rem;
	}
}