.gpt3__cta {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;

	padding: 2rem;
	margin: 4rem;
	border-radius: 1rem;
	background: var(--gradient-bar);
}

.gpt3__cta-content p {
	font-weight: 500;
	font-size: 12px;
	line-height: 2.5;
	color: #0E0E0E;
}

.gpt3__cta-content h3 {
	font-weight: 800;
	font-size: 24px;
	line-height: 1.88;
	color: #000;
}

.gpt3__cta-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: 2rem;
}

.gpt3__cta-btn button {
	background-color: #000;
	color: #fff;
	border-radius: 40px;
	font-weight: 700;
	font-size: 18px;
	line-height: 1.67;

	padding: 0.5rem 1rem;
	min-width: 150px;
	border: none;
	outline: none;
	cursor: pointer;
}

@media screen and (max-width: 650px) {
	.gpt3__cta {
		flex-direction: column;
	}

	.gpt3__cta-btn {
		margin: 2rem 0 0;
	}
}

@media screen and (max-width: 550px) {
	.gpt3__cta {
		margin: 4rem 2rem;
	}

	.gpt3__cta-content h3 {
		font-size: 18px;
		line-height: 1.7;
	}

	.gpt3__cta-btn button {
		font-size: 14px;
		line-height: 2;
	}
}