.gpt3__header {
	display: flex;
}

.gpt3__header-content {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-direction: column;
	margin-right: 5rem;
}

.gpt3__header-content h1 {
	font-weight: 800;
	font-size: 62px;
	line-height: 1.21;
	letter-spacing: -0.04em;
}

.gpt3__header-content p {
	font-size: 20px;
	line-height: 1.35;
	color: var(--color-text);
	margin-top: 1.5rem;
}

.gpt3__header-content__input {
	display: flex;
	width: 100%;
	margin: 2rem 0 1rem;
}

.gpt3__header-content__input input {
	flex: 2;
	width: 100%;
	min-height: 50px;

	padding: 0 2rem;
	outline: none;
	color: #fff;
	background-color: #052D56;
	border: 2px solid #052D56;
	font-size: 20px;
	line-height: 1.35;

	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
}

.gpt3__header-content__input button {
	flex: 0.6;
	width: 100%;
	min-height: 50px;

	background-color: #FF4820;
	font-weight: 700;
	font-size: 20px;
	line-height: 1.35;
	color: #fff;
	cursor: pointer;
	outline: none;
	padding: 0 1rem;
	border: none;

	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
}

.gpt3__header-content__people {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 100%;
	margin-top: 2rem;
}

.gpt3__header-content__people img {
	width: 181.79px;
	height: 38px;
}

.gpt3__header-content__people p {
	margin: 0 0 0 1rem;
	text-align: center;
	font-weight: 500;
	font-size: 12px;
	line-height: 3.13;
	color: #fff;
}

.gpt3__header-image {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}

.gpt3__header-image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

@media screen and (max-width: 1070px) {
	.gpt3__header {
		flex-direction: column;
	}

	.gpt3__header-content {
		margin: 0 0 3rem;
	}
}

@media screen and (max-width: 650px) {
	.gpt3__header h1 {
		font-size: 48px;
		line-height: 1.1;
	}

	.gpt3__header p {
		font-size: 16px;
	}

	.gpt3__header-content__people {
		flex-direction: column;
	}

	.gpt3__header-content__people p {
		margin: 0;
	}

	.gpt3__header-content__input input,
	.gpt3__header-content__input button {
		font-size: 16px;
	}
}

@media screen and (max-width: 490px) {
	.gpt3__header h1 {
		font-size: 36px;
	}

	.gpt3__header p {
		font-size: 14px;
	}

	.gpt3__header-content__input input,
	.gpt3__header-content__input button {
		font-size: 12px;
	}
}